* {
  margin: 0;
  padding: 0;
  background-color: #121212;
}

body {
  color: #E7E9EC;
}

main {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px,800px));
  grid-gap: 30px;
  padding: 12% 0 20px;
  justify-items: center;
  justify-content: center;
  align-items: start;
}

hr {
  border: none;
  height: 1px;
  color: #212121;
  background-color: #212121;
}

h2 {
  font-weight: 400;
  font-family: "Space Mono";
  font-size: 16px;
  text-transform: uppercase;
}

.contact {
  display: grid;
  justify-content: center;
  align-content: center;
  gap: 4px;
  grid-auto-flow: column;
}

.work {
  padding-top: 18%;
  width: 100%;
  text-align: left;
}

.links {
  padding-top: 10%;
  width: 100%;
  text-align: left;
}

.title-box {
  padding-bottom: 32px;
}

header {
  padding: 30px 10%;
  text-align: center;
}

.name {
  font-size: 16px;
  line-height: 64px;
  font-family: 'Space Mono';
  text-transform: uppercase;
}

.intro {
  font-size: 40px;
  line-height: 64px;
  font-weight: 400;
  text-align: center;  
}

.item-center {
  display: grid;
  grid-auto-flow: column;
  gap: 4px;
  align-items: center;
  justify-items: center;
}

.active {
  color: #A7D49B;
}

.intro a {
  font-weight: 400;
  color: #E7E9EC;
  font-size: 40px;
  font-style: italic;
  text-decoration: none;
  -webkit-transition: all .3s ease 0s;
    transition: all .3s ease 0s;
}

/* Row */

.row h1 {
  font-size: 32px;
  font-weight: 400;
}

.row p.date {
  font-size: 14px;
  line-height: 8px;
  padding-bottom: 8px;
  font-family: 'Space Mono';
  color: #A7D49B;
  text-transform: uppercase;
  margin-bottom:12px;
}

.row p.desc {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
}

.row a {
    font-size: 15px;
    font-family: 'Space Mono', monospace;
    text-transform: uppercase;
    font-style: normal;
    line-height: 10px;
}

.row a::after {
  content: " ↗";
  font-size: 18px;
}

/* Row end */

.title-box h2::after {
  content:" ↓";
  font-size: 18px;
}

.intro a:hover {
  color: #A7D49B;
}

.text--small {
  font-size: 15px;
  font-family: 'Space Mono', monospace;
  text-transform: uppercase;
}

.smiley--small {
  font-size: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .intro {
    font-size: 26px;
    line-height: 40px;
    padding: 24px;
    overflow-wrap: break-word;
  }

  p.date {
    line-height: 30px!important;
    margin-bottom: 0px!important;
  }
  
  .intro a {
    font-size: 26px;
  }

  .row a{
    font-size: 15px;
  }

  .row h1 {
    padding-top: 10px;
  }

  .links {
    padding-top: 16%;
  }

  .intro p {
    text-align: left;
  }

  .smiley--small {
    display: none;
  }

  .contact {
    display: none;
  }
}